export const SinglePageLanding = ({ setMessage, data, tickets, setLoaderBox }) => {
  const mapUrl = `https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3927.271724288086!2d76.37364157451023!3d10.158554170287491!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3b08061b468975ab%3A0xdeeba8a7f2436d7d!2sCIAL%20Convention%20Centre!5e0!3m2!1sen!2sin!4v1728543012977!5m2!1sen!2sin`;
  // Format dates using moment
  const start = moment(data.event.startDate);
  const end = moment(data.event.endDate);
  const theme = useSelector((state) => state.theme);
  // Check if start and end dates are the same day
  const isSameDay = start.isSame(end, "day");

  const ExpoCard = (item, index, customClass = "") => {
    const startDate = moment(item.startDate);
    const endDate = moment(item.endDate);

    const formatDateDisplay = () => {
      // If same day and same time
      if (startDate.isSame(endDate, "day") && startDate.hour() === endDate.hour() && startDate.minute() === endDate.minute()) {
        return startDate.format("Do MMMM YYYY, h:mm A");
      }

      // If same day (different times)
      if (startDate.isSame(endDate, "day")) {
        return startDate.format("Do MMMM YYYY, h:mm A") + " – " + endDate.format("h:mm A");
      }

      // If same month and year
      if (startDate.isSame(endDate, "month") && startDate.isSame(endDate, "year")) {
        return `${startDate.format("Do MMMM, h:mm A")} – ${endDate.format("Do MMMM, h:mm A")}${startDate.format(", YYYY")}`;
      }

      // If different months or years
      return `${startDate.format("Do MMMM YYYY, h:mm A")} – ${endDate.format("Do MMMM YYYY, h:mm A")}`;
    };

    return (
      <CountItem key={index} id={`countItem-${index}`} className={`${theme} ${customClass}`} event={data.event}>
        <Left>
          <img onError={(e) => (e.target.src = process.env.REACT_APP_CDN + data.event.banner)} src={process.env.REACT_APP_CDN + item.thumbnail} alt={item.title} />
        </Left>
        <Right event={data.event}>
          {/* {item.tag?.length > 0 && <h4>{item.tag}</h4>} */}
          <LinkButton
            onClick={() => {
              setMessage({
                type: 0,
                content: item.description,
                proceed: "Close",
                icon: "info",
              });
            }}
            className="h2"
          >
            {item.title}
          </LinkButton>
          <p> {item.description}</p>
          <h3>{formatDateDisplay()}</h3>
          <div>
            <LinkButton
              onClick={() => {
                setMessage({
                  type: 0,
                  content: item.description,
                  proceed: "Close",
                  icon: "info",
                });
              }}
              className="action remove"
            >
              Details <GetIcon icon={"info"} />
            </LinkButton>
            <EventRegister setLoaderBox={setLoaderBox} item={item} event={data.event} itemsList={[]} userToken={null} />
          </div>
        </Right>
      </CountItem>
    );
  };
  return (
    <>
      <Container>
        <Header>
          <Logo>
            <img src={process.env.REACT_APP_CDN + data.event.logo} alt="KEDDA Logo" />
          </Logo>
          <RegisterButton>Tickets</RegisterButton>
        </Header>

        <HeroSection>
          <img src={process.env.REACT_APP_CDN + data.event.banner} alt="Dental illustration" />
        </HeroSection>
        <Content>
          <Information>
            <EventDetails>
              <h2>{data.event.title}</h2>

              {isSameDay ? (
                <DateCards>
                  <DateCard>
                    <GetIcon icon="date" />
                    <div className="date-info">
                      <p className="date">{start.format("dddd, Do MMMM YYYY")}</p>
                      <p className="time">{start.format("h:mm A - ") + end.format("h:mm A")}</p>
                    </div>
                  </DateCard>
                </DateCards>
              ) : (
                <DateCards>
                  <DateCard>
                    <GetIcon icon="date" />
                    <div className="date-info">
                      <p className="date">{start.format("dddd, Do MMMM YYYY")}</p>
                      <p className="time">Starts at {start.format("h:mm A")}</p>
                    </div>
                  </DateCard>

                  <DateCard>
                    <GetIcon icon="date" />
                    <div className="date-info">
                      <p className="date">{end.format("dddd, Do MMMM YYYY")}</p>
                      <p className="time">Ends on {end.format("h:mm A")}</p>
                    </div>
                  </DateCard>
                </DateCards>
              )}

              <LocationCard>
                <GetIcon icon="location" size={24} color="#0284C7" />
                <div className="location-info">
                  <h4>{data.event.venue}</h4>
                  <p>{data.event.venue}</p>
                </div>
              </LocationCard>
            </EventDetails>
            <AboutSection>
              <h2>About Event</h2>
              <div dangerouslySetInnerHTML={{ __html: data.event.description }}></div>{" "}
            </AboutSection>
          </Information>
          {tickets.length === 1 && <OneTicket>{ExpoCard(tickets[1], 0, "single")}</OneTicket>}
        </Content>
        <Events>
          <h2>Tickets</h2>
          <FeaturesContainer>{tickets.map((item, index) => ExpoCard(item, index))}</FeaturesContainer>
        </Events>
        <LocationSection>
          <h2>Location</h2>
          <div className="map-container">
            <iframe title={data.event.title} src={mapUrl} style={{ border: "0" }} allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>{" "}
          </div>
        </LocationSection>
      </Container>

      <Footer>
        <div className="footer-content">
          <div className="copyright">
            <p>©{`${new Date().getFullYear()} ${data.event.title}`} All Rights Reserved | Developed by Eventhex</p>
          </div>
          <div className="social-links">
            <GetIcon icon="facebook" size={20} />
            <GetIcon icon="linkedin" size={20} />
            <GetIcon icon="instagram" size={20} />
            <GetIcon icon="x" size={20} />
          </div>
        </div>
      </Footer>

      <MobileFixed>
        <Container>
          <Floating>
            <div>
              <p>{tickets.length} Tickets</p>
            </div>
            <Button>View Tickets</Button>
          </Floating>
        </Container>
      </MobileFixed>
    </>
  );
};

export default SinglePageLanding;
